"use client";

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';

export default function Home() {
  const [url, setUrl] = useState('');
  const [file, setFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [extractedData, setExtractedData] = useState<any>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    const formData = new FormData();
    if (url) {
      formData.append('url', url);
    }
    if (file) {
      formData.append('file', file);
    }

    try {
      const response = await fetch('/api/extract', {
        method: 'POST',
        body: formData,
      });
      const data = await response.json();
      setExtractedData(data);
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">AI-Powered Web Interface Extraction</h1>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="url" className="block mb-2">Enter URL:</label>
          <Input
            type="url"
            id="url"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            placeholder="https://example.com"
          />
        </div>
        <div>
          <label htmlFor="file" className="block mb-2">Or upload a screenshot:</label>
          <Input
            type="file"
            id="file"
            accept="image/*"
            onChange={(e) => setFile(e.target.files?.[0] || null)}
          />
        </div>
        <Button type="submit" disabled={loading}>
          {loading ? 'Extracting...' : 'Extract Interface'}
        </Button>
      </form>

      {extractedData && (
        <div className="mt-8">
          <h2 className="text-xl font-semibold mb-4">Extracted Interface</h2>
          <pre>{JSON.stringify(extractedData, null, 2)}</pre>
        </div>
      )}
    </div>
  );
}
